:root {
	/* Typography */
	--font-sans: 'Inter', sans-serif;

	/* Color scheme */

	/* Colors */
	/*Старые для путей пользователя, потом убрать*/
	--color-primary: #1172cb;
	--color-background-header: #9ccaf4;
	--color-background-content: #c4dcf2;
	--color-input-placeholder: #8fa1b2;

	/*По макету*/
	--soft-grey: #9a98a8;
	--grey-color-50: #f0f2f4;
	--grey-color-90: #909090;
	--grey-gradient-400: #8995a9;
	--grey-gradient-600: #566276;
	--grey-color-500: #323944;
	--accent-color-violet: #6d47d1;
	--dark-violet: #403a6d;
	--dark-violet-gradient-200: #b3b0c5;
	--dark-violet-gradient-400: #66618a;
	--soft-violet: #a5a0c5;
	--primory-button-hover: #a083e0;
	--secondary-button-hover: #e1d6ff;
	--accent-color-salad: #c3ee11;

	/* Base */
	--color-base-black: #000;
	--color-base-white: #fff;

	/* Shadows */
	--card-shadow: 0px 0px 8px 0px rgba(17, 17, 26, 0.1), 0px 1px 0px 0px rgba(17, 17, 26, 0.05);

	/* Typography */
	/*Старые для путей пользователя, потом убрать*/
	--font-text-xl-regular: 400 36px/43.57px var(--font-sans);
	--font-text-lg-regular: 400 24px/29.05px var(--font-sans);

	/*По макету*/
	--font-text-13-108-400: 400 13px/14px var(--font-sans);
	--font-text-14-100-400: 400 14px/14px var(--font-sans);
	--font-text-14-120-400: 400 14px/1.2 var(--font-sans);
	--font-text-14-140-400: 400 14px/1.4 var(--font-sans);
	--font-text-16-120-400: 400 16px/1.2 var(--font-sans);
	--font-text-18-140-400: 400 18px/1.4 var(--font-sans);
	--font-text-24-120-400: 400 24px/1.2 var(--font-sans);
	--font-header-18-120-600: 600 18px/1.2 var(--font-sans);
	--font-header-24-120-600: 600 24px/1.2 var(--font-sans);
}
